.App {
  text-align: center;
  height: 100vh;
  margin: 0 auto;
  padding: 0;
  background-color: black;
}




.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header {
    padding: 2rem;
    /* margin: 1rem 0; */
    text-align: center;
    background-color: #3730A3;
    color: aliceblue;
    max-height: auto;
}
.headerTitle {
    font-weight: 400;
    font-size: 2.3rem;
    font-family: 'Roboto Mono', monospace;
    width: auto;
}
.aciklama{
    font-weight: 300;
    font-size: 1.5rem;
    font-family: 'Roboto Mono', monospace;
    width: auto;
    margin-bottom: 1rem;
}

@media screen and (max-width: 600px){
    .header {
        padding: 1rem;
        margin: 0rem 0rem;
        

    }
    .headerTitle {
        
        font-size: 14px;
        
    }
    .aciklama{
       
        font-size: 10px;
        
    }

}
.img-data{
    display: flex;
    flex-wrap: wrap;
    margin: 1rem 1rem;
    justify-content: space-around;
    width: 100%;
    height: auto;
}





/* CSS */
.button-1 {
  background-color: #EA4C89;
  border-radius: 8px;
  border-style: none;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  height: 40px;
  line-height: 20px;
  list-style: none;
  margin: 0;
  outline: none;
  padding: 10px 16px;
  position: relative;
  text-align: center;
  text-decoration: none;
  transition: color 100ms;
  vertical-align: baseline;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: 7rem;
}

.button-1:hover,
.button-1:focus {
  background-color: #F082AC;
}

