.data-item{
  display: flex;
  flex-direction: column;
  padding: 3rem 1rem;
  align-items: center;
  height: auto;
  
}

.buttons {
  display:flex;
   grid-gap: 10rem;
   gap: 10rem
}


.item-details{
  color:white;
  width: 20rem;
  padding-top: 1rem;
 }

 @media screen and (max-width: 1200px) {
  .img-data {
    flex-direction: column;
    margin: 0 0;
  }

  .data-item {
    padding: 0.3rem 1rem;
    display: flex;
  }

  .buttons {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
  }

  .react-button {
    width: 1rem;
  }
}

@media screen and (max-width: 600px) {
  .img-data {
    flex-direction: column;
    margin: 0 0;
  }

  .button-4,
  .button-3,
  .button-2,
  .button-1 {
    font-size: 8px;
    height: 20px;
    line-height: 10px;
    width: 60px;
    padding: 5px 8px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .buttons {
    margin-top: 1rem;
  }

  .data-item {
    padding: 0.3rem 1rem;
  }
}

